import React from "react";
import { connect } from "react-redux";
import { Todo, Auth, fetchTodos, deleteTodo, fetchAuth } from "../../../actions";
import { StoreState } from "../../../reducers";
// import { Link } from "react-router-dom";
import Alert, { AlertInterface } from "../../../components/Alert/Alert";
import { Link } from "react-router-dom";
import Modal, { Themes, ModalSize } from "../../../components/Modal/Modal";
// props for the component
interface AppProps {
  todos: Todo[];
  auth: Auth;
  fetchTodos: Function;
  deleteTodo: typeof deleteTodo;
  fetchAuth: Function;
}

interface AppState {
  fetching: boolean;
  users: string[];
}

class _Dashboard extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      fetching: false,
      users: ["janvier", "james", "Jonathan", "Alexi"],
    };
  }

  componentDidUpdate(prevProps: AppProps): void {
    if (!prevProps.todos.length && this.props.todos.length) {
      this.setState({ fetching: false });
    }
  }

  onButtolClick = (): void => {
    this.props.fetchTodos();
    this.setState({ fetching: true });
  };

  onButtolClickLogin = (): void => {
    this.props.fetchAuth();
    // this.setState({ fetching: true });
  };

  onTodoClick = (id: number): void => {
    this.props.deleteTodo(id);
  };

  render() {
    return (
      <div className="px-24">
        <div className="bg-hero mt-5 rounded p-8 h-48">
          <div>
            <span className="text-white text-4xl font-bold">Dashboard for preparing exam</span>
          </div>
        </div>
        <div className="mt-6 flex justify-center">
          <Link to="/method-add-question" className="bg-primary px-5 py-3 rounded text-white text-2xl mx-2">
            <span>Add a questions</span>
          </Link>
          <Link to="/questions-list" className="bg-green-600 px-5 py-3 rounded text-white text-2xl mx-2">
            <span>Question Bank</span>
          </Link>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  todos,
  auth,
}: StoreState): { todos: Todo[]; auth: Auth } => {
  return { todos: todos, auth: auth };
};

export const Dashboard = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
})(_Dashboard);
