import React, { Fragment, useState } from "react";
import { NavLink } from "react-router-dom";
import Logo from "./mineduc.svg";
import {
  PUBLIC,
  AUTHENTICATED,
  NON_AUTHENTICATED,
} from "../../config/AppNavigations";

import {
  MdMenu,
  MdSettings,
  MdNotifications,
  MdAccountCircle,
  MdClose,
  MdViewHeadline,
} from "react-icons/md";
import UserMenu from "./UserMenu";

interface NavigationProps {
  isAuthenticated: boolean;
}

const Navigation = ({ isAuthenticated }: NavigationProps) => {
  const [accountModal, setaccountModal] = useState(false);
  const [mobileNav, setMobileNav] = useState<boolean>(false);
  return (
    <nav className="bg-white shadow shadow-md fixed inset-x-0 top-0">
      <div className="max-w-7xl mx-auto px-2 sm:px-6 lg:px-24">
        <div className="relative flex items-center justify-between h-16">
          <div className="absolute inset-y-0 left-0 flex items-center sm:hidden">
            {/* Mobile menu button*/}
            <button
              type="button"
              className="inline-flex items-center justify-center p-2 rounded-md text-gray-400 hover:text-white hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white"
              aria-controls="mobile-menu"
              aria-expanded="false"
              onClick={() => setMobileNav(!mobileNav)}
            >
              <span className="sr-only">Open main menu</span>
              {mobileNav === true ? <MdClose className="text-3xl" /> : <MdMenu className="text-3xl" />}
            </button>
          </div>
          <div className="flex-1 flex items-center justify-center sm:items-stretch sm:justify-start">
            <div className="flex items-center">
              {/* Left items */}
              <span>
                <img className="w-8" src={Logo} alt="" />
              </span>
              <span className="text-2xl ml-2 font-bold uppercase text-gray-500">
                MINEDUC
              </span>
            </div>
          </div>
          <div className="absolute inset-y-0 right-0 flex items-center pr-2 sm:static sm:inset-auto sm:ml-6 sm:pr-0">
            {/* Right side menus */}
            <div className="hidden sm:block sm:ml-6 mr-6">
              <div className="flex space-x-4 items-center justify-end text-gray-500">
                {PUBLIC.map((link, i) => (
                  <NavLink
                    key={i}
                    exact
                    to={link.url}
                    className={`main-navigation`}
                  >
                    {link.title}
                  </NavLink>
                ))}

                {!isAuthenticated &&
                  NON_AUTHENTICATED.map((link, i) => (
                    <NavLink
                      key={i}
                      exact
                      to={link.url}
                      className={`main-navigation`}
                    >
                      {link.title}
                    </NavLink>
                  ))}

                {isAuthenticated &&
                  AUTHENTICATED.map((link, i) => (
                    <NavLink
                      key={i}
                      exact
                      to={link.url}
                      className={`main-navigation`}
                    >
                      {link.title}
                    </NavLink>
                  ))}
              </div>
            </div>

            {isAuthenticated === true && (
              <Fragment>
                <NavLink to="#" className="ml-6 hidden sm:block">
                  <MdSettings className="text-2xl text-gray-600" />
                </NavLink>
                <NavLink to="#" className="ml-6 hidden sm:block">
                  <MdNotifications className="text-2xl text-gray-600" />
                </NavLink>
                {/* Profile dropdown */}
                <div className="ml-6 relative">
                  <div>
                    <button
                      type="button"
                      className="flex text-sm rounded-full focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-gray-800 focus:ring-white"
                      id="user-menu-button"
                      aria-expanded="false"
                      aria-haspopup="true"
                      onClick={() => setaccountModal(!accountModal)}
                    >
                      <span className="sr-only">Open user menu</span>
                      <MdAccountCircle className="text-3xl text-gray-600" />
                    </button>
                  </div>
                  {/* Profile menus */}
                  {accountModal === true && <UserMenu />}
                </div>
              </Fragment>
            )}
          </div>
        </div>
      </div>
      {/* Mobile menu, show/hide based on menu state. */}
      {mobileNav === true &&
      <div className="sm:hidden" id="mobile-menu">
        <div className="px-2 pt-2 pb-3 space-y-1" onClick={() => mobileNav === true && setMobileNav(false)}>
          {PUBLIC.map((link, i) => (
            <NavLink
              key={i}
              exact
              to={link.url}
              className={`text-gray-500 block px-3 py-2 rounded-md text-base font-medium`}
            >
              {link.title}
            </NavLink>
          ))}

          {!isAuthenticated &&
            NON_AUTHENTICATED.map((link, i) => (
              <NavLink
                key={i}
                exact
                to={link.url}
                className={`text-gray-500 block px-3 py-2 rounded-md text-base font-medium`}
              >
                {link.title}
              </NavLink>
            ))}

          {isAuthenticated &&
            AUTHENTICATED.map((link, i) => (
              <NavLink
                key={i}
                exact
                to={link.url}
                className={`text-gray-500 block px-3 py-2 rounded-md text-base font-medium`}
              >
                {link.title}
              </NavLink>
            ))}
        </div>
      </div>
      }
    </nav>
  );
};

export default Navigation;
