import React, { useCallback, useState } from 'react'
import { Fragment } from 'react';
import { FileError, FileRejection, useDropzone } from 'react-dropzone'
import { FaCloudUploadAlt } from 'react-icons/fa';
import { RiDragMove2Line } from 'react-icons/ri';
import SingleUploadWithProgress from './SingleUploadWithProgress';
import UploadError from './UploadError';

export interface UploadableFile {
  file: File;
  errors: FileError[];
  url?: string;
}

const UploadFile = ({name}: {name: string}) => {
  
  const [files, setFiles] = useState<UploadableFile[]>([]);
  const onDrop = useCallback((acceptedFiles: File[], rejectedFiles: FileRejection[]) => {
    const mappedAccepted = acceptedFiles.map(file => ({file, errors: []}));
    setFiles(currentFiles => [...currentFiles, ...mappedAccepted, ...rejectedFiles]);
  }, [])

  const onDelete = (file: File) => {
    setFiles(curr => curr.filter(fw => fw.file !== file));
  }

  const onUpload = (file: File, url: string) => {
    setFiles((curr) => 
      curr.map((fw) => {
        if (fw.file === file) {
          return {...fw, url};
        }
        return fw;
      })
    );
  }

  const {getRootProps, getInputProps, isDragActive} = useDropzone({onDrop, accept: ['image/*', 'video/*', '.pdf'], maxSize: 300 * 1024})

  return (
    <Fragment>
      <div>
        <div {...getRootProps({className: 'dropzone py-8 border-2 border-dashed mb-5 mt-3 mx-2 bg-gray-100 cursor-pointer'})}>
          <input {...getInputProps()} />
          {
          isDragActive ?
            <div className="flex flex-col justify-center items-center">
              <RiDragMove2Line className="text-8xl" />
              Drop the files here ...
            </div> :
            <div className="flex flex-col justify-center items-center">
              <FaCloudUploadAlt className="text-8xl" />
              Drag 'n' drop some files here, or click to select files</div>
          }
        </div>
      </div>
      {files.map((fileWrapper, idx) => (
        <div className="my-3">
          {fileWrapper.errors.length ? <div className="text-red-500">
            <UploadError file={fileWrapper.file} onDelete={onDelete} errors={fileWrapper.errors} />
          </div> :
          <SingleUploadWithProgress onUpload={onUpload} onDelete={onDelete} key={idx} file={fileWrapper.file} />}
        </div>
      ))}
    </Fragment>
  )
}

export default UploadFile
