import React from 'react'
import { MdDeleteSweep } from 'react-icons/md'
export interface FileHeaderProps {
  file: File;
  onDelete: (file: File) => void;
}

const FileHeader = ({file, onDelete}: FileHeaderProps) => {
  return (
    <div className="flex flex-row justify-between items-center gap-2">
      <div className="text-xs font-light">{file.name}</div>
      <div>
        <button onClick={() => onDelete(file)} className="text-gray-500 text-sm px-3 py-1 border rounded bg-white">Delete</button>
      </div>
    </div>
  )
}

export default FileHeader
