import React, { Fragment } from 'react'
import { FileError } from 'react-dropzone'
import FileHeader from './FileHeader'

export interface UploadErrorProps {
  file: File;
  onDelete: (file: File) => void;
  errors: FileError[];
}

const UploadError = ({file, onDelete, errors}: UploadErrorProps) => {
  return (
    <Fragment>
      <div className="mt-5 px-2 rounded bg-red-50 pt-2">
        <FileHeader file={file} onDelete={onDelete} />
        <div className="relative pt-0">
          <div className="text-right">
            <span className="text-xs font-semibold inline-block text-pink-600">
              0%
            </span>
          </div>
          <div className="overflow-hidden h-2 mb-4 text-xs flex rounded bg-pink-200">
            <div style={{ width: "0%" }} className="shadow-none flex flex-col text-center whitespace-nowrap text-white justify-center bg-pink-500"></div>
          </div>            
          <div className="pb-1 italic font-bold text-xs">
            {errors.map((error) => (
              <div>Error: {error.message}</div>
            ))}
          </div>
        </div>
      </div>
    </Fragment>
  )
}

export default UploadError
