import React from 'react'
import ApplicationStepItem, { ApplicationStepInterface } from './ApplicationStepItem'

export enum ApplicationStepsTypes {
  Step1, Step2, Step3
}

interface ApplicationStepsProps {
  currentStep: ApplicationStepsTypes;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
}

const ApplicationSteps = ({currentStep, setApplicationStep}: ApplicationStepsProps) => {
  const steps: ApplicationStepInterface[] = [
    {
      step: ApplicationStepsTypes.Step1,
      stepNumber: 1,
      title: "Requirements",
      description: "Check the position and its requirements",
    },
    {
      step: ApplicationStepsTypes.Step2,
      stepNumber: 2,
      title: "Psychometric test",
      description: "Do psychometric test",
    },
    {
      step: ApplicationStepsTypes.Step3,
      stepNumber: 3,
      title: "Exam centre",
      description: "Choose the examination centre",
    },
  ]
  return (
    <div className="grid md:grid-cols-3 gap-2 w-full mt-4">
      {steps.map((item: ApplicationStepInterface, i: number) => (
        <ApplicationStepItem 
          currentStep={currentStep}
          stepItem={item}
          setApplicationStep={setApplicationStep}
        />
      ))}
    </div>
  )
}

export default ApplicationSteps
