import React from "react";
import { connect } from "react-redux";
import { Todo, Auth, fetchTodos, deleteTodo, fetchAuth } from "../../actions";
import { StoreState } from "../../reducers";
// import { Link } from "react-router-dom";
import Alert, { AlertInterface } from "../../components/Alert/Alert";
// props for the component
interface AppProps {
  todos: Todo[];
  auth: Auth;
  fetchTodos: Function;
  deleteTodo: typeof deleteTodo;
  fetchAuth: Function;
}

interface AppState {
  fetching: boolean;
  users: string[];
}

class _Login extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      fetching: false,
      users: ["janvier", "james", "Jonathan", "Alexi"],
    };
  }

  componentDidUpdate(prevProps: AppProps): void {
    if (!prevProps.todos.length && this.props.todos.length) {
      this.setState({ fetching: false });
    }
  }

  onButtolClick = (): void => {
    this.props.fetchTodos();
    this.setState({ fetching: true });
  };

  onButtolClickLogin = (): void => {
    this.props.fetchAuth();
    // this.setState({ fetching: true });
  };

  onTodoClick = (id: number): void => {
    this.props.deleteTodo(id);
  };

  render() {
    return (
      <div className="p-5">
        <button className="btn bg-primary text-white hover:bg-blue-600">
          Click me
        </button>

        <div className="pt-6 mt-2 bg-white p-4">
          <input type="text" className="input input-border" />
        </div>

        <div className="pt-6 mt-2 bg-gray-200 p-4">
          <input type="text" className="input-white" />
        </div>

        <div className="p-4">
          <Alert
            title="Hello"
            description="helo this the description"
            theme={AlertInterface.danger}
          />
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  todos,
  auth,
}: StoreState): { todos: Todo[]; auth: Auth } => {
  return { todos: todos, auth: auth };
};

export const Login = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
})(_Login);
