import React, { useState } from 'react'
import { useEffect } from 'react'
import FileHeader from './FileHeader';



export interface SingleUploadWithProgressProps {
  file: File;
  onDelete: (file: File) => void;
  onUpload: (file: File, url: string) => void;
}

const SingleUploadWithProgress = ({file, onDelete, onUpload}: SingleUploadWithProgressProps) => {
  const [progress, setProgress] = useState(0);

  const uploadfile = (file: File, onProgress: (percentage: number) => void) => {
    const url = 'https://api.cloudinary.com/v1_1/demo/image/upload';
    const key = 'docs_upload_example_us_preset';
  
    return new Promise<string>((res, rej) => {
      const xhr = new XMLHttpRequest();
      xhr.open('POST', url);
      xhr.onload = () => {
        const resp = JSON.parse(xhr.responseText)
        res(resp.secure_url);
      }
      xhr.onerror = (evt) => rej(evt);
      xhr.upload.onprogress = (event) => {
        if (event.lengthComputable) {
          const percentage = (event.loaded/event.total)*100;
          onProgress(Math.round(percentage))
        }
      };
      const formData = new FormData();
      formData.append('file', file);
      formData.append('upload_preset', key);
      xhr.send(formData);
    })
  }

  const upload = async () => {
    const url = await uploadfile(file, setProgress);
    onUpload(file, url);
    // console.log('URL: ', url);
  }

  useEffect(() => {
    upload();
  }, [])
  return (
    <div className={`mt-5 pt-2 px-2 rounded text-${progress === 100 ? "green" : "gray"}-600 bg-${progress === 100 ? "green-50" : "gray-100"}`}>
      <FileHeader file={file} onDelete={onDelete} />
      <div className="relative pt-0">
        <div className="flex items-center justify-between">
          <div>
            {/* <span className="text-xs font-semibold inline-block py-1 px-2 uppercase rounded-full text-blue-600 bg-blue-200">
              Task in progress
            </span> */}
          </div>
          <div className="text-right">
            <span className={`text-xs font-semibold inline-block text-${progress === 100 ? "green" : "gray"}-400`}>
              {progress}%
            </span>
          </div>
        </div>
        <div className={`overflow-hidden h-2 mb-4 text-xs flex rounded bg-${progress === 100 ? "green" : "gray"}-200`}>
          <div style={{ width: `${progress}%` }} className={`shadow-none flex flex-col text-center whitespace-nowrap text-white justify-center bg-${progress === 100 ? "green" : "gray"}-500`}></div>
        </div>
      </div>
    </div>
  )
}

export default SingleUploadWithProgress
