import React, { Component } from 'react'
import { MdAddCircle, MdArrowForward, MdCheckBox, MdChevronRight, MdDescription, MdEdit } from 'react-icons/md';
import ApplicationPositions, { ApplicationPositionInterface } from './ApplicationPositions/ApplicationPositions';
import ApplicationSteps, { ApplicationStepsTypes } from './ApplicationSteps/ApplicationSteps';
import PropTypes from 'prop-types'
import PositionRequirement from './PositionRequirement/PositionRequirement';
import DoTest from './DoTest/DoTest';
import ExamCenter from './ExamCenter/ExamCenter';

interface ApplicationState {
	currentStep: ApplicationStepsTypes;
  currentPosition: ApplicationPositionInterface | null;
}

interface ApplicationProps {}

export class Application extends Component<ApplicationProps, ApplicationState> {
  constructor(props: ApplicationProps) {
		super(props);

		this.state = {
      currentStep: ApplicationStepsTypes.Step1,
      currentPosition: null,
		};
	}
  setApplicationStep = (step: ApplicationStepsTypes) => {
    this.setState({ currentStep: step });
  }
  setCurrentPosition = (position: ApplicationPositionInterface | null) => {
    this.setState({ currentPosition: position});
  }
  render() {
    return (
      <div className="px-24">
        {/* Step 2 */}
        {this.state.currentStep === ApplicationStepsTypes.Step2 ? (
          <DoTest 
            currentPosition={this.state.currentPosition}
            setCurrentPosition={this.setCurrentPosition}
            setApplicationStep={this.setApplicationStep}
          />
        ) : (
          <>
            <div className="bg-hero mt-5 rounded px-5 pt-5 pb-1">
              <div className="flex flex-col">
                <span className="text-white text-4xl font-bold text-opacity-75">Create new application</span>
                <ApplicationSteps
                  currentStep={this.state.currentStep}
                  setApplicationStep={this.setApplicationStep}
                />
              </div>
            </div>

            <div className="my-4 bg-white shadow rounded  px-2 py-2">
              <div className="w-full">
              {/* Step one */}
              {this.state.currentStep === ApplicationStepsTypes.Step1 && (
                this.state.currentPosition === null ? (
                  <ApplicationPositions
                    currentPosition={this.state.currentPosition}
                    setCurrentPosition={this.setCurrentPosition}
                  />
                ) : (
                  <PositionRequirement
                    currentPosition={this.state.currentPosition}
                    setCurrentPosition={this.setCurrentPosition}
                    setApplicationStep={this.setApplicationStep}
                  />
                ))}
                {/* Step 3 */}
                {this.state.currentStep === ApplicationStepsTypes.Step3 && (
                  <ExamCenter 
                    currentPosition={this.state.currentPosition}
                    setCurrentPosition={this.setCurrentPosition}
                    setApplicationStep={this.setApplicationStep}
                  />
                )}
              </div>
            </div>
          </>
        )}
        
      </div>
    )
  }
}

export default Application