import React from 'react'
import { ApplicationStepsTypes } from './ApplicationSteps'

export interface ApplicationStepInterface {
  step: ApplicationStepsTypes;
  stepNumber: number;
  title: string;
  description: string;
}

export interface ApplicationStepItemProps {
  currentStep: ApplicationStepsTypes;
  stepItem: ApplicationStepInterface;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
}

const ApplicationStepItem = ({ currentStep, stepItem, setApplicationStep }: ApplicationStepItemProps) => {
  return (
    currentStep === stepItem.step ? (
      <div className="border-b-4 border-white border-opacity-60 bg-black bg-opacity-20 px-4 py-4 rounded-t-md shadow-md flex items-center">
        <div className="flex flex-row">
          <div className="h-12 w-12 rounded-full flex justify-center items-center bg-transparent border-2 border-white border-opacity-60"><span className="text-3xl text-white text-opacity-60 font-bold">{stepItem.stepNumber}</span></div>
          <div className="flex flex-col pl-3 text-white text-opacity-80 font-light">
            <span className="text-2xl">{stepItem.title}</span>
            <span className="text-xs">{stepItem.description}</span>
          </div>
        </div>
      </div>
    ) : (
      <div onClick={() => setApplicationStep(stepItem.step)} className="border-b-4 border-transparent bg-transparent bg-opacity-20 px-4 py-4 flex items-center cursor-pointer">
        <div className="flex flex-row">
          <div className="h-12 w-12 rounded-full flex justify-center items-center bg-transparent border-2 border-white"><span className="font-light text-3xl text-white">{stepItem.stepNumber}</span></div>
          <div className="flex flex-col pl-3 text-white font-light">
            <span className="text-2xl">{stepItem.title}</span>
            <span className="text-xs">{stepItem.description}</span>
          </div>
        </div>
      </div>
    )
  )
}

export default ApplicationStepItem
