/**
 * @description Modal design
 * @done_by Niyongabo
 */
import { MdClose } from 'react-icons/md';

export enum Themes {
  default = "default",
  primary = "primary",
  secondary = "secondary",
  danger = "danger",
  success = "success",
  warning = "warning"
}

export enum ModalSize {
  small = "max-w-sm",
  medium = "max-w-screen-sm",
  large = "max-w-screen-md",
  extraLarge = "max-w-screen-lg",
  maxWidth = "max-w-screen"
}

interface ModalInterface {
  title?: any;
  backDrop: boolean;
  theme: Themes;
  close: Function;
  backDropClose: boolean;
  footer?: any;
  children: any;
  widthSizeClass: ModalSize;
  displayClose: boolean;
  padding: { title?: boolean, body?: boolean, footer?: boolean }
}

const Modal = (props: ModalInterface): JSX.Element => {
  const { 
    title, 
    backDrop, 
    theme, 
    close, 
    backDropClose, 
    widthSizeClass, 
    displayClose,
    padding = { title: true, body: true, footer: true }
   }: ModalInterface = props;

  let themeColor: string;

  switch (theme) {
    case Themes.primary:
      themeColor = "blue-500";
      break;
    case Themes.secondary:
      themeColor = "gray-300";
      break;
    case Themes.danger:
      themeColor = "red-500";
      break;
    case Themes.success:
      themeColor = "green-500";
      break;
    case Themes.warning:
      themeColor = "yellow-500";
      break;
    default:
      themeColor = "white";
      break;
  }
  return (
    <div onClick={(): void => backDropClose && close()} className={`modal bg-black bg-opacity-${backDrop === true ? '60' : '0'}`}>
      <div className={`bg-white ${widthSizeClass} rounded shadow-xl relative`} style={{width: '100%', maxHeight: '99vh', overflowY: 'scroll', margin: '30px auto', marginBottom: '30px', zIndex: 99999999}}>
        <div className={`flex justify-between bg-${themeColor} ${padding.title === true ? "py-4 px-4" : "" } rounded-t text-${theme === Themes.default || theme === Themes.secondary ? "black" : "white"}`}>
          {!title || title === "" ? <div></div> : (<h4 className={`text-lg font-bold `}>{title}</h4>)}
          {displayClose === false ? "" : <MdClose className="cursor-pointer font-extrabold" onClick={():void => close()} />}
        </div>
        <div className={`space-x-3 ${padding.body === true ? "py-4 px-4" : "" }`}>
          <div>{props.children !== "" ? props.children : ""}</div>
        </div>
        {props.footer !== undefined && props.footer !== "" ? (
          <div className={`flex float-right ${padding.footer === true ? "py-4 px-4" : "" }`}>{props.footer}</div>
        ) : ""}
      </div>
    </div>
  );
};

export default Modal;
